import {
    Box,
    Button,
    Container,
    FormControl,
    FormControlLabel,
    FormLabel,
    Link,
    Paper,
    Radio,
    RadioGroup,
    SwipeableDrawer,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TextField,
    Typography,
} from '@mui/material';
import {useEffect, useMemo, useRef} from 'react';
import Header from '../../components/Header';
import data8369 from '../../drawData/data8369';
import data8614 from '../../drawData/data8614';
import dataDestinyPet from '../../drawData/dataDestinyPet';
import dataDestinyWeapon from '../../drawData/dataDestinyWeapon';
import GrabArea from './GrabArea';
import useStore from './useStore';
const data = {
    data8369,
    data8614,
    dataDestinyWeapon,
    dataDestinyPet,
};

const useZState = (key) => {
    const value = useStore((state) => state[key]);
    const setValue = (value) => {
        useStore.setState({
            [key]: value,
        });
    };
    return [value, setValue];
};
function VirtualDrawScreen() {
    const [drawNums, setDrawNums] = useZState('drawNums');
    const [nowDataKey, setNowDataKey] = useZState('nowDataKey');
    const [drawData, setDrawData] = useZState('drawData');
    const [text, setText] = useZState('text');
    const [average] = useZState('average');
    const [total] = useZState('total');
    const cleanResult = useStore((state) => state.cleanResult);
    const resultList = useStore((state) => state.resultList);
    const formatData = (data) => {
        return data
            .trim()
            .split('\n')
            .map((value, index) => {
                const [name, chance] = value.split('\t');
                return {id: index, name, chance: parseFloat(chance.slice(0, -1))};
            });
    };
    const pSum = useMemo(
        () =>
            Math.round(drawData.reduce((previousValue, currentValue) => previousValue + currentValue.chance, 0) * 100) /
            100,
        [drawData]
    ); //
    const draw = useStore((state) => state.draw);
    const _bottom = useRef();
    const _scroll = useRef();

    useEffect(() => {
        const scrollHeight = _scroll.current.scrollHeight;
        const height = _scroll.current.clientHeight;
        const maxScrollTop = scrollHeight - height;
        _scroll.current.scrollTop = maxScrollTop > 0 ? maxScrollTop : 0;
    }, [resultList]);
    const first = useRef(true);
    useEffect(() => {
        if (first.current === false || drawData.length === 0) {
            setDrawData(formatData(data[nowDataKey]));
        }
        first.current = false;
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [nowDataKey]);
    const [drawerOpen, setDrawerOpen] = useZState('drawerOpen');
    const openDrawer = () => setDrawerOpen(true);
    const closeDrawer = () => setDrawerOpen(false);

    return (
        <div>
            <Header title="">
                <Button color="inherit" onClick={openDrawer}>
                    
                </Button>
            </Header>
            <SwipeableDrawer
                BackdropProps={{style: {backgroundColor: 'transparent'}}}
                anchor="right"
                open={drawerOpen}
                onClose={closeDrawer}
                onOpen={() => {}}
            >
                <Box sx={{width: 350, p: 2}}>
                    <Link
                        target="_blank"
                        rel="noopener"
                        href="https://tw.beanfun.com/beanfuncommon/EventAD_Mobile/EventAD.aspx?EventADID=5325"
                    >
                        
                    </Link>
                    <Box sx={{marginTop: 2}}>
                        <FormControl>
                            <FormLabel></FormLabel>
                            <RadioGroup
                                value={nowDataKey}
                                onChange={(event) => {
                                    cleanResult();
                                    setNowDataKey(event.target.value);
                                }}
                            >
                                <FormControlLabel
                                    value="data8369"
                                    control={<Radio />}
                                    label={''}
                                />
                                <FormControlLabel value="data8614" control={<Radio />} label="" />
                                <FormControlLabel
                                    value="dataDestinyWeapon"
                                    control={<Radio />}
                                    label=""
                                />
                                <FormControlLabel value="dataDestinyPet" control={<Radio />} label="" />
                                {nowDataKey === '' && (
                                    <FormControlLabel value="" control={<Radio />} label="" />
                                )}
                            </RadioGroup>
                        </FormControl>
                    </Box>
                    <GrabArea />
                </Box>
            </SwipeableDrawer>
            <Container sx={{marginTop: 5}}>
                <Box
                    sx={{
                        display: 'flex',
                        flexDirection: {
                            xs: 'column-reverse',
                            sm: 'row',
                        },
                    }}
                >
                    <Box sx={{display: 'flex', flexDirection: 'column', flex: 1}}>
                        <Box
                            sx={{
                                display: 'flex',
                                marginTop: 1,
                            }}
                        >
                            <TextField
                                label=""
                                multiline
                                maxRows={4}
                                value={text}
                                onChange={(event) => {
                                    setText(event.target.value);
                                }}
                            />
                            <Button
                                onClick={() => {
                                    if (text !== '') {
                                        setDrawData(formatData(text));
                                        cleanResult();
                                    }
                                }}
                            >
                                
                            </Button>
                        </Box>
                        <Typography sx={{my: 2}}>{pSum}%</Typography>
                        <TableContainer component={Paper}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell></TableCell>
                                        <TableCell align="right"></TableCell>
                                        <TableCell align="right"></TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {drawData.map(({id, name, chance, count}) => (
                                        <TableRow key={id}>
                                            <TableCell>{name}</TableCell>
                                            <TableCell align="right">{chance}%</TableCell>
                                            <TableCell align="right">{count}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Box>
                    <Box sx={{mx: 2}} />
                    <Box sx={{display: 'flex', flexDirection: 'column', flex: 1}}>
                        <Box sx={{display: 'flex', alignItems: 'center', marginTop: 1}}>
                            <TextField
                                label=""
                                value={drawNums}
                                onChange={(event) => {
                                    const value = event.target.value;
                                    if (/^[1-9]\d*$/.test(value) || value === '') {
                                        setDrawNums(value);
                                    }
                                }}
                            />
                            <Button
                                onClick={() => {
                                    draw();
                                }}
                            >
                                
                            </Button>
                        </Box>
                        <Box sx={{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
                            <Typography sx={{my: 2, mr: 1}}>{total}</Typography>
                            {!!average && <Typography>{Math.round(average * 100) / 100}</Typography>}
                            <Button onClick={cleanResult}></Button>
                        </Box>
                        <Paper
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                height: {
                                    xs: 150,
                                    sm: 600,
                                },
                                overflow: 'auto',
                                px: 5,
                            }}
                            ref={_scroll}
                        >
                            {resultList.map((value, index) => (
                                <Box key={index} sx={{marginBottom: 1}}>
                                    {value}
                                </Box>
                            ))}
                            <div ref={_bottom}></div>
                        </Paper>
                    </Box>
                </Box>
            </Container>
        </div>
    );
}
export default VirtualDrawScreen;
